/**
 * Clase DarkModeToggle: Administra el modo oscuro en la pgina web.
 */
class DarkModeToggle {
	/**
	 * Constructor de la clase.
	 * @param {string} buttonSelector - Selector del botn que activa/desactiva el modo oscuro.
	 */
	constructor(buttonSelector) {
		// Seleccionar el botn mediante el selector proporcionado.
		this.darkModeButton = document.querySelector(buttonSelector);

		// Verifica si existe el botn.
		if (!this.darkModeButton) {
			console.warn(
				`El selector '${buttonSelector}' no coincide con ninguo de los botones de la pgina.`
			);
			return;
		}

		// Verificar si el sistema prefiere el modo oscuro.
		this.prefersDarkMode = window.matchMedia("(prefers-color-scheme: dark)");

		// Inicializar el modo oscuro segn la preferencia del usuario.
		this.toggleDarkMode();

		// Agregar un listener para cambios en la preferencia de color del sistema.
		this.prefersDarkMode.addEventListener(
			"change",
			this.toggleDarkMode.bind(this)
		);

		// Agregar un listener al botn para alternar manualmente el modo oscuro.
		this.darkModeButton.addEventListener("click", this.toggleDarkMode.bind(this));
	}

	/**
	 * Mtodo para alternar el modo oscuro en el cuerpo de la pgina.
	 */
	toggleDarkMode() {
		// Alternar la clase "dark-mode" en el cuerpo de la pgina segn la preferencia del usuario.
		document.body.classList.toggle("dark-mode", this.prefersDarkMode.matches);
	}
}
